.switch-theme {
  width: 49px;
  height: 25px;
  cursor: pointer;
  border: 1px solid var(--white);
  background-color: #191a19;
  border-radius: 50px;
  position: absolute;
  top: 180px;
  right: 220px;
}

.switch-theme:hover, .switch-theme:focus {
  border: 1px solid var(--orange);
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  perspective: 1000px;
  animation: shake .82s cubic-bezier(.36, .07, .19, .97) both;
  transform: translate3d(0, 0, 0);
}

@keyframes shake {
  10%, 90% {
    transform: translate3d(-1px, 0, 0);
  }

  20%, 80% {
    transform: translate3d(2px, 0, 0);
  }

  30%, 50%, 70% {
    transform: translate3d(-4px, 0, 0);
  }

  40%, 60% {
    transform: translate3d(4px, 0, 0);
  }
}

#switch {
  display: none;
  position: absolute;
}

.switch-theme__slider {
  width: 100%;
  height: 100%;
  border-radius: 50px;
  transition: translate .25s;
  position: absolute;
}

input:checked ~ .switch-theme__slider {
  background-color: var(--white);
}

.switch-theme__slider:before {
  content: "";
  width: 20px;
  height: 20px;
  background-color: #191a19;
  border-radius: 50%;
  transition: all .6s;
  position: absolute;
  top: 2px;
  left: 4px;
  box-shadow: inset 7px -4px #d8dbe0;
}

input:checked ~ .switch-theme__slider:before {
  background-color: var(--orange);
  box-shadow: none;
  transform: translateX(23px);
}

